import React, { useState } from 'react';
import { Link } from "react-router-dom";

import "../css/header.css";

const spacing = {
	margin: '0 0.3rem',
};

const HeaderLeft = () => (
	<div className="flex_group">
		<div>
		<a className="logo-link" href="/">Jeff Zhao</a>
		</div>
		<div style={spacing}/>
		<NonHamburgerMenu/>
	</div>
);

const NonHamburgerMenu = () => (
	<div className="non-hamburger-menu">
	<Link className="header-link" to="/">Home</Link>
	<Link className="header-link" to="/experiences">Experiences</Link>
	<Link className="header-link" to="/projects">Projects</Link>
	</div>
);

function HamburgerMenu(props) {
	const [ visible, setVisible ] = useState(false);

	async function handleVisible(event) {
		event.preventDefault();
		setVisible(!visible);
		console.log(visible);
	}

	if (!visible) {
		return (
		<div className="hamburger-menu" onClick={e => handleVisible(e)}>
			<div className="bar1"></div>
			<div className="bar2"></div>
			<div className="bar3"></div>
		</div>
		);
	}

	return (
	<div>
	<div className="hamburger-button change" onClick={e => handleVisible(e)}>
		<div className="bar1"></div>
		<div className="bar2"></div>
		<div className="bar3"></div>
	</div>
	<div className="hamburger-menu">
		<ul>
			<li><Link className="header-link" to="/">Home</Link></li>
			<li><Link className="header-link" to="/experiences">Experiences</Link></li>
			<li><Link className="header-link" to="/projects">Projects</Link></li>
		</ul>
	</div>
	</div>
	);
}

function Header(props) {
	return (
<nav className="topnav">
	<HeaderLeft/>
	<div className="flex_group">
		<HamburgerMenu/>
	</div>
</nav>
	);
}

export default Header;
export { Header };
